import React, { useState } from 'react';
import { GlobalStore } from 'global-store-hook';

function ListOwnItem(props) {
  const {currency, color} = props;
  const [isActive, setisActive] = useState(false);
  const store = GlobalStore();
  const activeItem = () => {
    setisActive(!isActive)
    store.set('isAmount', !isActive);
  }
  return (
    <li className={isActive ? 'checkbox-ic' : ''} onClick={activeItem}>
      <div className={`list-own-list__ico ico-bg_${color}`}><span className={`currency-icon icon-${currency}-ic`}></span></div>
      <div className="list-own-list__name"> 94,384.98</div>
      <div className="list-own-list__id">A/C ending: 9847</div>
    </li>
  )
}
export default ListOwnItem;