import React from 'react';
import './invest.scss';
import InvestCard from './invest-card';
import netflix from '../../img/netflix.png';
import airbnb from '../../img/airbnb.png';
import farfetch from '../../img/farfetch.png';
import huawei from '../../img/huawei.png';
import firefox from '../../img/firefox.png';
import payoneer from '../../img/payoneer.png';
import w from '../../img/w.png';
import amazon from '../../img/amazon.png';

function Investments() {
  const arrInvest = [
    {
      img: netflix,
      name: 'Netflix',
      country: 'USA'
    },
    {
      img: airbnb,
      name: 'Airbnb',
      country: 'USA'
    },
    {
      img: farfetch,
      name: 'Farfetch',
      country: 'Great Britain'
    },
    {
      img: huawei,
      name: 'Huawei',
      country: 'China'
    },
    {
      img: firefox,
      name: 'Firefox',
      country: 'USA'
    },
    {
      img: payoneer,
      name: 'Payoneer',
      country: 'USA'
    },
    {
      img: amazon,
      name: 'Amazon',
      country: 'Great Britain'
    },
    {
      img: w,
      name: 'Volkswagen',
      country: 'Germany'
    },
  ]
  return (
    <div className="container-responsive">
      <div className="box-white comming-soon">
        <div>
          <div className="comming-soon__title">Investments  are comming soon.</div>
          <div className="comming-soon__text">Explain the coming features and product benefits in few lines.</div>
        </div>
      </div>
      <div className="invest-list">
        {arrInvest.map(i => <InvestCard {...i} />)}
      </div>
    </div>
  );
}
export default Investments;
