import React, { useState } from 'react';
import ListOwnItem from './list-own-item'

function ListOwn(props) {
  const [isShowMore, setIsShowMore] = useState(false);
  return (
    <div className="list-own">
      <ul className={`list-own-list ${isShowMore ? 'show-more' : ''}`}>
        <ListOwnItem currency="gbr" color="orange" handleClick="" />
        <ListOwnItem currency="eur" color="green" />
        <ListOwnItem currency="gbr" color="yellow" />
        <ListOwnItem currency="gbr" color="orange" />
        <ListOwnItem currency="eur" color="green" />
        <ListOwnItem currency="gbr" color="yellow" />
      </ul>
      <div className="btn-show-more">
        <div className={`${!isShowMore ? '' : 'hidden'}`} onClick={() => setIsShowMore(true)}>Show more <span className="icon-arrow-ic"></span></div>
        <div className={`${isShowMore ? '' : 'hidden'}`} onClick={() => setIsShowMore(false)}>Hide <span className="icon-arrow-ic rotate"></span></div>
      </div>
    </div>
  )
}
export default ListOwn;