import React from 'react';

function InvestCard(props) {
  const {country, name, img} = props;
  return (
    <div className="box-white invest-list__item">
      <div className="invest-list__logo">
        <img src={img} alt="img" />
      </div>
      <div className="invest-list__name">{name}<span className="invest-list__country">{country}</span></div>
      <div className="invest-list__description">Online private lodging service<br/>operating worldwise</div>
      <div className="invest-list-table">
        <div className="invest-list-table__row">
          <div className="invest-list-table__col">Yield</div>
          <div className="invest-list-table__col">4.2%</div>
        </div>
        <div className="invest-list-table__row">
          <div className="invest-list-table__col">Price</div>
          <div className="invest-list-table__col">105.8%</div>
        </div>
        <div className="invest-list-table__row">
          <div className="invest-list-table__col">Min amount</div>
          <div className="invest-list-table__col">$100,000</div>
        </div>
        <div className="invest-list-table__row">
          <div className="invest-list-table__col">Fees</div>
          <div className="invest-list-table__col">0.5% entry fee</div>
        </div>
      </div>
      <button className="button button_full">Register interest</button>
    </div>
  )
}
export default InvestCard;