import React from 'react';
import { GlobalStore } from 'global-store-hook';

function AmountBox(props) {
  const store = GlobalStore();

  return store.get('isAmount') && (
    <div>
      <div className="name-white-box">Enter payment amount</div>
      <div className="box-white amount-box">
        <div className="amount-box__sum">
          I am sending:
          <div className="icon-with-input">
            <span className="icon-with-input__icon icon-gbr-ic"></span>
            <input className="input-field input-field_icon" value="980" />
          </div>
        </div>
        <div className="amount-box__payment_reference">
          Payment reference
          <div><input className="input-field" placeholder="e.g. Invoice 01" value="" /></div>
        </div>
      </div>
      <button className="amount-box-btn button button__primary button_large-more">Review</button>
    </div>
  )
}
export default AmountBox;