import React, { useState } from 'react';
import './send.scss';
import Select, { components } from 'react-select'
import reloadIc from '../../img/reload-ic.png'
import docdIc from '../../img/doc-ic.png'
import ListOwn from '../../components/list-own';
import AmountBox from '../../components/amount-box';
const options = [
  { value: '43,456.90', currency: 'gbr', label: 'A/C Ending  4554', icon: 'orange' },
  { value: '483,456.90', currency: 'eur', label: 'A/C Ending  4554', icon: 'green' },
  { value: '434,456', currency: 'gbr', label: 'A/C Ending  4554', icon: 'yellow' }
]
const formatPrice = (price) => {
  const PRICE = price.split('.');
  return {
    sum: PRICE[0],
    cent: PRICE[1] | ''
  }
}
const { Option } = components;
const customStyles = {
  control: (styles) => ({
    ...styles,
    border: 0,
    borderBottom: '1px solid #053e5e',
    borderRadius: 0,
    height: '38px',
    boxShadow: 0,
  }),
  menu: styles => ({ ...styles, border: 0, borderRadius: 0, marginTop: 0, boxShadow: '0 2px 33px 0 rgba(0, 0, 0, 0.04)' }),
  indicatorSeparator: () => ({display: 'none'}),
  valueContainer: () => ({height: '100%', width: '244px'}),
  dropdownIndicator: styles => ({ ...styles, border: 0, padding: 0, color: '#053e5e', fontSize: '24px'})
}
const customSingleValue = ({ data }) => (
  <div className="input-select">
      <div className="input-select__single-value">
        { data.icon && <span className={`ico-bg ico-bg_${data.icon} icon-${data.currency}-ic`}></span> }
        <span className="input-select__name">{ formatPrice(data.value).sum }<span>.{formatPrice(data.value).cent}</span></span>
        <span className="input-select__label">{ data.label }</span>
      </div>
  </div>
);
const customOption = (props) => (
  <Option className="custom-option" {...props}>
      { props.data.icon && <span className={`ico-bg ico-bg_${props.data.icon} icon-${props.data.currency}-ic`}></span> }
      <span className="input-select__name">{ formatPrice(props.data.value).sum }<span>.{formatPrice(props.data.value).cent}</span></span>
      <span className="input-select__label">{ props.data.label }</span>
  </Option>
);
const DropdownIndicator = props => {
  return (
    components.DropdownIndicator && (
      <components.DropdownIndicator {...props}>
        <span className={`icon-arrow-ic ${props.selectProps.menuIsOpen? 'open-select' : ''}`}></span>
      </components.DropdownIndicator>
    )
  );
};
console.log('send');
function Investments() {
  const [active, setActive] = useState('Payees');
  const [isOwnPayees, setownPayees] = useState(false);
  return (
    <div>
      <div className="container-send">
        <div className="box-white container-send__account">
          <div className="container-send__select-box">
            <div className="container-send__account-title">Source account</div>
            <Select options={options}
              // menuIsOpen="false"
              styles={customStyles}
              onChange={() => setownPayees(true)}
              components={ {SingleValue: customSingleValue, Option: customOption, DropdownIndicator } }
            />
          </div>
          <div className="container-send__ivoice-box">
            <div className="container-send__ivoice-name">Upload an invoice, and we will prepare<br/>a payment order for you to approve.</div>
            <button className="button button_large button_full">Upload an invoice</button>
          </div>
        </div>
        <div className="box-white container-send__payments-sign">
          <div className="container-send__payments-title">Payments ready to sign</div>
          <ul className="container-send__payments-list">
            <li className="payments-list-item">
              <div className="payments-list-item__icon"><img src={docdIc} alt="" /></div>
              <div>
                <div className="payments-list-item__title">200.<span>00</span>  23 Mar 2020</div>
                <div className="payments-list-item__label">payment ready for review</div>
              </div>
              <div></div>
            </li>
            <li className="payments-list-item">
              <div className="payments-list-item__icon"><img src={reloadIc} alt="" /></div>
              <div>
                <div className="payments-list-item__title">5 invoices enqueued</div>
              </div>
              <div></div>
            </li>
          </ul>
        </div>
      </div>
      <div className={`box-own-payees ${!isOwnPayees ? 'hidden' : ''}`}>
        <div className="tabs">
          <div className="tabs-nav">
            <div className={`tab ${active === 'Payees' ? 'active' : ''}`} onClick={()=>setActive('Payees')}>Payees</div>
            <div className={`tab ${active === 'Own_accounts' ? 'active' : ''}`} onClick={()=>setActive('Own_accounts')}>Own accounts</div>
          </div>
          <div id="tab1" className={`tab-content ${active === 'Payees' ? 'active' : ''}`}>
            <ul className="payees-list">
              <li>
                <div className="payees-list__ico">MP</div>
                <div className="payees-list__name">Matthew Patton</div>
                <div className="payees-list__id">GB23LLOYD28374658212</div>
              </li>
              <li>
                <div className="payees-list__ico">NR</div>
                <div className="payees-list__name">Nelle Reyes</div>
                <div className="payees-list__id">GB23LLOYD28374658212</div>
              </li>
              <li>
                <div className="payees-list__ico">MC</div>
                <div className="payees-list__name">Micheal Curry</div>
                <div className="payees-list__id">GB23LLOYD28374658212</div>
              </li>
            </ul>
          </div>
          <div id="tab2" className={`tab-content ${active === 'Own_accounts' ? 'active' : ''}`}>
            {active === 'Own_accounts' && <ListOwn />}
          </div>
        </div>
      </div>
      <AmountBox />
    </div>
    
  );
}
export default Investments;
